@use '../../../../sass/variables' as v;
@forward '../../../../sass/templates';

//Main wrapper

.contact-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  font-family: v.$font-family;
  padding-bottom: v.$components-gap;
  background-image: url('../../../../assets/background-png/bg-component-down.svg');
  background-size: cover;

  //Contact container

  .contact-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    width: v.$mobile-s-width;

    //Title

    .contact-title {
      @extend %titleBars;
    }

    //Small text at top

    .contact-text {
      text-align: center;
      width: 280px;
    }

    //Main container for form

    .contact-form-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: v.$contact-form-s;
      background-color: v.$main-white;
      border-radius: 1rem;

      //Actual form

      .contact-form {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        width: v.$contact-form-s-inner;
        padding: 15px 0px 15px 0px;

        //Form field for each input

        .contact-form-field {
          position: relative;
          display: flex;
          flex-direction: column;
          gap: 0.5rem;

          //Label

          .contact-form-label {
            font-size: v.$fs-m;
            font-family: v.$font-family;
          }

          //Input - Input
          .contact-form-input {
            font-family: v.$font-family;
            padding: 5px;
            padding-left: 10px;
            color: v.$main-black;
            background-color: #e7e7e7;
            border: 2px solid transparent;
            border-radius: 1rem;
            outline: none;

            //***************** Validation - name *****************//

            //- hu placeholder

            &:not(:focus) + .hu::after {
              position: absolute;
              left: 10px;
              content: 'Kérem irjon egy érvényes nevet';
              font-size: v.$fs-xs;
            }

            &:valid + .hu::after {
              position: absolute;
              left: 10px;
              content: 'Érvényes név';
              font-size: v.$fs-xs;
              color: green;
              transition: 0.3s ease;
            }

            //- en placeholder

            &:not(:focus) + .en::after {
              position: absolute;
              left: 10px;
              content: 'Please write a valid name';
              font-size: v.$fs-xs;
            }

            &:valid + .en::after {
              position: absolute;
              left: 10px;
              content: 'Valid name';
              font-size: v.$fs-xs;
              color: green;
              transition: 0.3s ease;
            }

            //Border - color

            &:valid {
              border: 2px solid green;
              transition: 0.3s ease;
            }

            &:invalid {
              border: 2px solid red;
              transition: 0.3s ease;
            }

            &:is(:placeholder-shown) {
              border: 2px solid transparent;
              transition: 0.3s ease;
            }

            &::placeholder {
              font-family: v.$font-family;
            }
          }

          //Input - Email
          .contact-form-email {
            font-family: v.$font-family;
            padding: 5px;
            padding-left: 10px;
            color: v.$main-black;
            background-color: #e7e7e7;
            border: 2px solid transparent;
            border-radius: 1rem;
            outline: none;

            //***************** Validation - email *****************//

            //- hu placeholder

            &:not(:focus) + .hu::after {
              position: absolute;
              left: 10px;
              content: 'Kérem irjon egy érvényes email cimet';
              font-size: v.$fs-xs;
            }

            &:valid + .hu::after {
              position: absolute;
              left: 10px;
              content: 'Érvényes email';
              font-size: v.$fs-xs;
              color: green;
              transition: 0.3s ease;
            }

            //- en placeholder

            &:not(:focus) + .en::after {
              position: absolute;
              left: 10px;
              content: 'Please write a valid email';
              font-size: v.$fs-xs;
            }

            &:valid + .en::after {
              position: absolute;
              left: 10px;
              content: 'Valid email';
              font-size: v.$fs-xs;
              color: green;
              transition: 0.3s ease;
            }

            //Border - color

            &:valid {
              border: 2px solid green;
              transition: 0.3s ease;
            }

            &:invalid {
              border: 2px solid red;
              transition: 0.3s ease;
            }

            &:is(:placeholder-shown) {
              border: 2px solid transparent;
              transition: 0.3s ease;
            }

            &::placeholder {
              font-family: v.$font-family;
            }
          }

          //Input - Textarea
          .contact-form-textarea {
            font-family: v.$font-family;
            padding-top: 10px;
            padding-left: 10px;
            border: none;
            color: v.$main-black;
            background-color: #e7e7e7;
            border-radius: 1rem;
            outline: none;
            resize: none;
            border: 2px solid transparent;

            //***************** Validation - textarea *****************//

            //- hu placeholder

            &:not(:focus) + .hu::after {
              position: absolute;
              left: 10px;
              content: 'Kérem irjon egy üzenetett';
              font-size: v.$fs-xs;
            }

            &:valid + .hu::after {
              position: absolute;
              left: 10px;
              content: 'Érvényes üzenet';
              font-size: v.$fs-xs;
              color: green;
              transition: 0.3s ease;
            }

            //- en placeholder

            &:not(:focus) + .en::after {
              position: absolute;
              left: 10px;
              content: 'Please write a message';
              font-size: v.$fs-xs;
            }

            &:valid + .en::after {
              position: absolute;
              left: 10px;
              content: 'Valid message';
              font-size: v.$fs-xs;
              color: green;
              transition: 0.3s ease;
            }

            //Border - color

            &:valid {
              border: 2px solid green;
              transition: 0.3s ease;
            }

            &:invalid {
              border: 2px solid red;
              transition: 0.3s ease;
            }

            &:is(:placeholder-shown) {
              border: 2px solid transparent;
              transition: 0.3s ease;
            }

            &::placeholder {
              font-family: v.$font-family;
            }
          }
        }

        //Submit button

        .submit-btn-container {
          display: flex;
          justify-content: center;
          width: 100%;

          .btn-submit {
            font-family: v.$font-family;
            padding: 15px 55px 15px 55px;
            border: none;
            border-radius: 5px;
            color: v.$main-black;
            background-color: v.$main-soft-yellow;
            font-size: v.$fs-xxl;
            margin-bottom: v.$components-gap;
            box-shadow: 0px 0px 15px 1px v.$main-lime-green;

            &:is(:hover, :focus-visible, :active) {
              cursor: pointer;
              transform: translateY(-3px);
              transition: 0.3s ease-in;

              p {
                color: v.$main-gray;
                transform: translateX(-8px);
                transition: 0.3s ease-in;
                text-shadow: 3px 3px v.$main-black;
              }
            }
          }
        }
      }
    }
  }
}

//Media queries

//******** Breakpoint - sm 410px ********//

@media only screen and (min-width: v.$breakpoint-sm) {
  .contact-container {
    width: v.$mobile-sm-width !important;
    transition: 1s ease-in-out;

    .contact-form-container {
      width: v.$contact-form-sm !important;
      transition: 1s ease-in-out;

      .contact-form {
        width: v.$contact-form-sm-inner !important;
        transition: 1s ease-in-out;

        .submit-btn-container {
          .btn-submit {
            width: v.$mobile-sm-width !important;
          }
        }
      }
    }
  }
}

//******** Breakpoint - m 576px ********//

@media only screen and (min-width: v.$breakpoint-m) {
  .contact-container {
    width: v.$mobile-m-width !important;
    transition: 1s ease-in-out;

    .contact-text {
      width: v.$mobile-sm-width !important;
    }

    .contact-form-container {
      width: v.$contact-form-m !important;
      transition: 1s ease-in-out;

      .contact-form {
        width: v.$contact-form-m-inner !important;
        transition: 1s ease-in-out;

        .btn-submit {
          width: v.$mobile-sm-width !important;
        }
      }
    }
  }
}

//******** Breakpoint - md 768px ********//

@media only screen and (min-width: v.$breakpoint-md) {
  .contact-container {
    width: v.$tablet-width !important;
    transition: 1s ease-in-out;

    .contact-text {
      width: v.$mobile-m-width !important;
    }

    .contact-form-container {
      width: v.$contact-form-md !important;
      transition: 1s ease-in-out;

      .contact-form {
        width: v.$contact-form-md-inner !important;
        transition: 1s ease-in-out;

        .btn-submit {
          width: v.$mobile-sm-width !important;
        }
      }
    }
  }
}

//******** Breakpoint - lg 992px ********//

@media only screen and (min-width: v.$breakpoint-lg) {
  .contact-container {
    width: v.$desktop-m-width !important;
    transition: 1s ease-in-out;

    .contact-text {
      width: v.$mobile-m-width !important;
    }

    .contact-form-container {
      width: v.$contact-form-md !important;
      transition: 1s ease-in-out;
      border: 2px solid v.$main-light-pink;

      .contact-form {
        width: v.$contact-form-md-inner !important;
        transition: 1s ease-in-out;

        .btn-submit {
          width: v.$mobile-sm-width !important;
        }
      }
    }
  }
}

//******** Breakpoint - xl 1200px ********//

@media only screen and (min-width: v.$breakpoint-xl) {
  .contact-container {
    width: v.$desktop-l-width !important;
    transition: 1s ease-in-out;

    .contact-text {
      width: v.$tablet-width !important;
    }

    .contact-form-container {
      width: v.$contact-form-xl !important;
      transition: 1s ease-in-out;

      .contact-form {
        width: v.$contact-form-xl-inner !important;
        transition: 1s ease-in-out;

        .btn-submit {
          width: v.$mobile-sm-width !important;
        }
      }
    }
  }
}

//******** Breakpoint - xl 1400px ********//

@media only screen and (min-width: v.$breakpoint-xxl) {
  .contact-container {
    width: v.$desktop-xl-width !important;
    transition: 1s ease-in-out;

    .contact-text {
      width: v.$tablet-width !important;
    }

    .contact-form-container {
      width: v.$contact-form-xl !important;
      transition: 1s ease-in-out;

      .contact-form {
        width: v.$contact-form-xl-inner !important;
        transition: 1s ease-in-out;

        .btn-submit {
          width: v.$mobile-sm-width !important;
        }
      }
    }
  }
}
